<template>
  <div>
    <Lunbo />
    <div class="g-center">
      <div class="g-nav flex">
        <span class="iconfont icon-information"></span>
        <h3>{{bd_news.category_title}}</h3>
        <div class="time-nav">
          <a :class="{'b-active' : news_index===index}" href="#" @click="$refs.hero.swiper.slideTo(index)" v-for="(list,index) in bd_news.news_list" :key="index">{{list.news_type}}</a>
          <!-- <a :class="{'b-active' : news_index===1}" href="#" @click="$refs.hero.swiper.slideTo(1)">周</a>
          <a :class="{'b-active' : news_index===2}" href="#" @click="$refs.hero.swiper.slideTo(2)">月</a> -->
        </div>
      </div>
      <div class="hot-list">
        <swiper
          :options="{autoHeight:true,onSlideChangeEnd:function(){news_index = $refs.hero.swiper.realIndex}}"
          ref="hero"
        >
          <swiper-slide v-for="(list,index) in bd_news.news_list" :key="index">
            <ul v-for="(item,i) in list.type_news_list" :key="i" v-show="btn_t || i<3">
              <li class="list-1 flex"  v-if="i===0">
                <div class="hot-img" >
                  <img :src="item.news_img" alt />
                  <span class="iconfont icon-bofang"></span>
                </div>
                <div class="hot-title">
                  <span class="hot-l">{{item.news_title}}</span>
                  <div class="flex hot-r">
                    <div>
                      <span class="iconfont icon-shipin"></span>
                      <span>{{item.news_num}}</span>
                    </div>
                    <div>
                      <span class="iconfont icon-shijian"></span>
                      <span>{{item.news_date}}</span>
                    </div>
                  </div>
                </div>
              </li>
              <li class="list-2 flex" v-else>
                <div class="list-2-r ellipsis">{{item.news_title}}</div>
                <div class="list-2-l">
                  <span class="iconfont icon-shipin"></span>
                  <span>{{item.news_num}}</span>
                </div>
              </li>
            
            </ul>
          </swiper-slide>
        </swiper>
      </div>
      <div class="hot-btn" @click="changeBtn()">
          {{btn_t ? "收起榜单":"查看完整榜单"}}     
        </div>
    </div>
    <div class="g-icont">
         <div class="g-nav flex">
            <span class="iconfont icon-information"></span>
            <h3>英雄攻略</h3>       
        </div>
        <div class="img-list">
            <ul class="flex">
                <li>
                    <img src="../../static/images/501.jpg">
                    <span>热门</span>
                </li>
                <li>
                    <img src="../../static/images/502.jpg">
                    <span>热门</span>
                </li>
                <li>
                    <img src="../../static/images/503.jpg">
                    <span>热门</span>
                </li>
                <li>
                    <img src="../../static/images/504.jpg">
                    <span>限免</span>
                </li>
                <li>
                    <img src="../../static/images/505.jpg">
                    <span>限免</span>
                </li>
                <li>
                    <img src="../../static/images/506.jpg">
                    <span>最新</span>
                </li>
                <li class="p-text" @click="show_bt = !show_bt">
                    <p>{{show_bt?'收起':'展开'}}</p>
                </li>                
            </ul>
            <div class="ic-list flex" v-show="show_bt">
                <div class="ic-l">                  
                            <div class="imgs-list flex"  v-for="(list,index) in heros_list.news_list" :key="index" v-show="hr_index===index">
                            <a href="" v-for="(item,i) in list.type_news_list" :key="i">
                                <img  class="imgs" :src="item.img_url" alt="" >
                                <p>{{item.name}}</p>
                            </a>                                
                            </div>    
                </div>
                <div class="ic-r">
                    <ul class="flex">
                        <li v-for="(items,index) in heros_list.news_list" :key="index" @click="changeHr(index)" :class="{'gds' : hr_index===index}">{{items.news_type}}</li>                        
                    </ul>
                </div>
            </div>
        </div>
    </div>
    <div style="width:100%;height:200px;background:#fff;margin-top:20px"></div>
  </div>
</template>
<script>
import bd_news from '../data/bd_news.json'
import herosData from "../data/heros.json"
import Lunbo from "../components/Lunbo.vue";
export default {  
  data() {
    return {
      news_index: 0,
      active: 0,
      btn_t:false,
      bd_news:{},
      heros_list:{},
      hr_index:0,
      show_bt:false,
    }
  },
  methods: {
    changeIndex() {
      this.news_index = this.$refs.hero.swiper.realIndex;
    },
     changeBtn() {
      this.btn_t = !this.btn_t;
    },
    changeHr(index) {
      this.hr_index = index;
    },
  },
  computed:{

  },
   mounted() { 
    this.$nextTick(()=>{
       this.bd_news=bd_news[0] 
       this.heros_list=herosData[0]     
    })      
  },
  components: {
    Lunbo
  }
};
</script>
<style lang="scss" >
.g-center {
  width: 100%;
  margin-top: 0.7692rem;
  background: #fff;
  .g-nav {
    padding: 0 0.7692rem;
    height: 4.1538rem;
    line-height: 4.1538rem;
    justify-content: space-between;
    padding-bottom: 0.7692rem;
    border-bottom: 0.0769rem solid #f1f1f1;
    span {
      font-size: 2rem;
      color: #999;
    }
    h3 {
      flex: 1;
      font-size: 1.3846rem;
      font-weight: 700;
      padding-left: 0.6rem;
    }
    a {
      padding: 0.3077rem 0.3077rem;
      font-size: 1.0769rem;
      color: #999;
    }
  }
  .hot-list {
    padding: 0 0.7692rem;
    .list-1 {
      width: 100%;
      height: 72.0005px;
      padding: 6px 0;
      padding-bottom: 0.7692rem;
      border-bottom: 0.0769rem solid #f1f1f1;
      justify-content: space-between;
      .hot-img {
        width: 12.3077rem;
        position: relative;
        img {
          display: block;
          width: 100%;
          height: 100%;
        }
        span {
          display: block;
          position: absolute;
          top: 50%;
          left: 50%;
          margin-left: -1.5385rem;
          margin-top: -1.5385rem;
          font-size: 3.0769rem;
          height: 3.0769rem;
          opacity: 0.6;
        }
      }
      .hot-title {
        padding-left: 0.6154rem;
        line-height: 1.6923rem;
        .hot-l {
          font-size: 1.1538rem;
          color: #343440;
        }
        .hot-r {
          color: #7a7a80;
          font-size: 0.9231rem;
          .iconfont {
            font-size: 0.7692rem;
          }
        }
      }
    }
    .list-2 {
      color: #343440;
      min-width: 0;
      font-size: 1rem;
      height: 2.7692rem;
      line-height: 2.7692rem;
      padding-bottom: 0.7692rem;
      border-bottom: 0.0769rem solid #f1f1f1;
      justify-content: space-between;
      .list-2-l {
        margin-left: 0.7692rem;
        color: #7a7a80;
        width: 6.1538rem;
        text-align: right;
        span {
          font-size: 0.9231rem !important;
          line-height: 0;
        }
      }
    }
  }
  .hot-btn {
    width: 100%;
    height: 2.6154rem;
    color: #7a7a80;
    background: #fff;
    text-align: center;
    line-height: 2.6154rem;
    border-top: 0.0769rem solid #f1f1f1;
  }
}
.g-icont{
    width: 100%;
    margin-top: 0.7692rem;
    background: #fff;
    .g-nav {
        padding: 0 0.7692rem;
        height: 4.1538rem;
        line-height: 4.1538rem;
        justify-content: space-between;
        padding-bottom: 0.7692rem;
        border-bottom: 0.0769rem solid #f1f1f1;
        span {
            font-size: 2rem;
            color: #999;
        }
        h3 {
            flex: 1;
            font-size: 1.3846rem;
            font-weight: 700;
            padding-left: 0.6rem;
        }
        a {
            padding: 0.3077rem 0.3077rem;
            font-size: 1.0769rem;
            color: #999;
        }
    }   
    .img-list{
        width: 100%;
        height: 4.6538rem;
        padding: 0.7692rem 0.7692rem;
        position: relative;       
        ul{
            justify-content: space-between;
            font-size:0.9231rem;
           
            li{
                position: relative;
                width: 3.1154rem;
                height: 3.1154rem; 
                                            
                img{
                    width: 100%;
                }
                span{
                    text-align: center;
                    position:absolute;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    background: #f1f1f1;
                    opacity: 0.6;
                }
               
            }
            .p-text{
                text-align: center;
                line-height: 3.1154rem;
                background: #f1f1f1; 
            }
        }
        .ic-list{
            position: absolute;
            left:0.7692rem;
            top:4.6154rem;
            width: 94.1%;
            height: 22.6154rem;                       
            background:#fff;
            .ic-l{
                width: 75%;
                height: 100%; 
                margin-right:0.7692rem ;                
                .imgs-list{
                    width: 100%; 
                    height: 100%;                                           
                    flex-wrap: wrap;  
                    overflow:auto;
                    a{            
                        width: 25%;                                  
                        text-align: center;   
                        padding:4px 2px;
                        
                        color:#333;                              
                        .imgs{              
                            width: 100%;
                        }
                        p{                           
                            font-size: 0.6rem;
                        }
                    }
                }                
            }
            .ic-r{
                width: 25%;
                height: 100%;
                background:#f1f1f1;
                ul{
                    justify-content: space-between;
                    flex-direction:column;   
                    height: 100%;
                    li{
                        line-height: 3.2308rem;                       
                        width: 100%;
                        height: 100%;
                        text-align: center;
                    }
                }
            }
        }
       
    }
}
</style>


